:root {
  // Colors
  --blue-primary: #1676f3;
  --boring-gray: #efefef;

  // Breakpoints
  --sm: 576px;
  --md: 768px;
  --lg: 992px;
  --xl: 1200px;
}

* {
  box-sizing: border-box;
  margin: 0;
  font-family: "Open Sans", sans-serif;
}

body {
  margin: 60px 40px 0 40px;

  @media (max-width: 576px) {
    margin-left: auto;
    margin-right: auto;
  }
}

.flex-container {
  display: flex;
}

.flex-column {
  flex-direction: column;
}

a {
  color: inherit;
  text-decoration: none;
}

.bold {
  font-weight: 600;
}
